@use './_reset';
@use './_mixin' as *;
@use './_common' as c;


.style_1{
    background: c.$bg;
    margin-top: -1%;
    .item_inner{
        background: #fff;

        .itemph{
            padding-top: calc(59 / c.$spw) * 100%;
        }
        
    }

    .style1-1{
        .styleph1-1{
            width: calc(222 / 324) * 100%;
            margin: calc(13 / 324) * 100% auto calc(8 / 324) * 100%;
        }
    
        .style_pop{
            right: 0;
        }
        .style_pop1{
            margin-top: calc(29 / 324) * 100%;
            margin-right: calc(31 / 324) * 100%;
        }
        .style_pop2{
            margin-top: calc(71 / 324) * 100%;
            margin-right: calc(55.7 / 324) * 100%;
        }

    }

    .style1-2{
        .styleph1-2{
            width: calc(220 / 324) * 100%;
            margin: calc(13 / 324) * 100% auto calc(17 / 324) * 100% calc(44 / 324) * 100%;
        }
    
        .style_pop{
            left: 0;
        }
        .style_pop1{
            margin-top: calc(54 / 324) * 100%;
            margin-left: calc(50 / 324) * 100%;
        }
        .style_pop2{
            margin-top: calc(126 / 324) * 100%;
            margin-left: calc(26 / 324) * 100%;
        }
    }
}


